/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-hide-or-toggle-simple-blocks {
	border:0;      		
	text-align: left;
}

.wp-block-hide-or-toggle-simple-blocks .title{
	background-color: inherit; 
	color: inherit;
	margin-right: 1em;
	margin-top:1px;	
	padding: 1em;
	border-style: solid;
	border-width: 1px;
	border-radius: 1px;
	cursor: pointer;
}

.wp-block-hide-or-toggle-simple-blocks .hidden  {
	background-color: inherit; 
	color: inherit;
	margin-left: 0;
	margin-right: 1em;
	margin-top: 1em;	
	border-style: solid;
	border-width: 1px;
	border-radius: 1px;
	cursor: pointer;
	display: none;
}
